@import "src/common/sass/variables";
.skillContainer {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 200px;
  margin-bottom: 15px;

  .iconContainer {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    .iconBody {
      width: 80px;
      height: 80px;
      background-color: $backgroundColorDark;
      rotate: 45deg;
      margin: 20px;
      background-size: cover;
      background-position: center;
    }

    .iconImg {
      position: absolute;
      background-position: center;
      background-size: 100%;
      background-repeat: no-repeat;
    }
  }

  .titleSkill {
    margin: 5px;
    font-size: 20px;
    margin-bottom: 15px;
    font-weight: 500;
    color: $backgroundColorDark;
    font-family: 'Poppins', sans-serif;
    line-height: 1.5em;
  }


}







