@import "src/common/sass/variables";
.navbar {
    display: flex;
    justify-content: space-around;
    width: 280px;
    margin-right: 45px;
    a {
        text-decoration: none;
        color: $backgroundColor; ;
        font-family: 'Poppins', sans-serif;
        line-height: 1.5em;
        font-size: 18px;
        margin-left: 10px;
        &:hover{
            cursor: pointer;
        }

    }
    .active{
        text-decoration: underline;
    }

}


